.imageFullPreview {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: relative;
}

.container {
  outline: none;
}

.image {
  flex: 0 1 100%;
  display: flex;
  justify-content: center;
}
.image > img {
  max-width: 100vw;
  max-height: 80vh;
}
